<template>
    <div class="search">

        <input type="text" v-model="userName" placeholder="type username" @keydown.enter="getUser(userName), userName = '', getUsers()">
        <button class="add" @click="getUser(userName), userName = '', getUsers()">SEARCH</button>

    </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
    data() {
        return {
            userName: ''
        }
    },

    methods: {
        ...mapActions(['getUser', 'getUsers'])
    },

    computed: {

    }
}
</script>