import { createStore } from 'vuex';
import axios from 'axios';

export default createStore({
  state: {
    user: null,
    sortName: null,
    sortRepos: null,
    sortDate: null,
  },

  getters: {
  },

  mutations: {
    getUser(state, payload) {
      state.user = payload;
    },

    getName(state, payload) {
      state.sortName = payload
      
      
    },

    getRepos(state, payload) {
      state.sortRepos = payload
    },

    getDate(state, payload) {
      state.sortDate = payload
    }
  },

  actions: {
    async getUser(context, userName) {
      try {
        const result = await axios.get(
          `https://api.github.com/users/${userName}`
        );
        console.log(result);

        let userInfo = { ...result.data };

        context.commit('getUser', userInfo);
      } catch (error) {
        console.error(error);
      }
    },

    async getUsers(context) {

      const result = await axios.get(
        `https://api.github.com/users`
      );

      let users = []

      for (let i = 0; i < 30; i++) {
        let us = await axios.get(
          `${result.data[i].url}`
        );
        users.push(us.data)
      }

      const log = 'login'
      const repos = 'public_repos'
      const date = 'created_at'

      const sortedName = users.sort((a, b) => {
        if (a[log].toLowerCase() < b[log].toLowerCase()) {
        return -1
      } if (a[log].toLowerCase() > b[log].toLowerCase()) {
        return 0
      }}).slice(0, 4)

      const sortedRepos = users.sort((a, b) => a[repos] < b[repos] ? 1 : -1).slice(0, 4)
      const sortedDate = users.sort((a, b) => a[date] > b[date] ? 1 : -1).slice(0, 4)

      console.log(sortedName);

      context.commit('getName', sortedName)
      context.commit('getRepos', sortedRepos)
      context.commit('getDate', sortedDate)
      },

      
      
    },

  },


);
